<template>
  <ul class="nav nav-tabs nav-stacked">
    <li class="nav-item" v-for="category in categories" @click="selectedCategory(category)">
      <a class="nav-link" v-bind:class="{ 'active': currentCategory === category }">{{category.name}}</a>
    </li>
  </ul>
</template>
<script>
  export default {
    props: ['currentCategory', 'categories'],
    data () {
      return {}
    },
    methods: {
      selectedCategory (item) {
        this.currentCategory = item
        this.$dispatch('selected-category', item)
      }
    }
  }
</script>
<style lang="scss">
.nav {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.nav-link {
  display: inline-block;
}

.nav-link:focus, .nav-link:hover {
  text-decoration: none;
}

.nav-link.disabled {
  color: #818a91;
}

.nav-link.disabled, .nav-link.disabled:focus, .nav-link.disabled:hover {
  color: #818a91;
  cursor: not-allowed;
  background-color: transparent;
}

.nav-inline .nav-item {
  display: inline-block;
}

.nav-inline .nav-item + .nav-item,
.nav-inline .nav-link + .nav-link {
  margin-left: 1rem;
}

.nav-tabs::after {
  display: table;
  clear: both;
  content: "";
}

.nav-tabs .nav-item {
  float: left;
  margin-bottom: -4px;
}

.nav-tabs .nav-item + .nav-item {
  margin-left: .2rem;
}

.nav-tabs .nav-link {
  display: block;
  padding: .5em 1em;
  border: 1px solid #ddd;
}

.nav-tabs .nav-item:first-child .nav-link, .nav-tabs .nav-item:last-child .nav-link {
  border-top: 0px solid transparent;
}

.nav-tabs .nav-link:focus, .nav-tabs .nav-link:hover {
  border-color: #eceeef transparent #eceeef #ddd;
}

.nav-tabs .nav-link.disabled, .nav-tabs .nav-link.disabled:focus, .nav-tabs .nav-link.disabled:hover {
  color: #818a91;
  background-color: transparent;
  border-color: transparent;
}

.nav-tabs .nav-link.active, .nav-tabs .nav-link.active:focus, .nav-tabs .nav-link.active:hover,
.nav-tabs .nav-item.open .nav-link,
.nav-tabs .nav-item.open .nav-link:focus,
.nav-tabs .nav-item.open .nav-link:hover {
  color: #55595c;
  background-color: #fff;
  border-color: #ddd transparent #ddd red;
  border-left-width: 2px;
}

.nav-tabs .nav-item:last-child .nav-link.active {
  border-top: 1px solid #ddd;
}

.nav-stacked .nav-item {
  display: block;
  float: none;
}

.nav-stacked .nav-item + .nav-item {
  margin-top: .2rem;
  margin-left: 0;
}
</style>



