<template>
  <div class="weui_panel">
    <div class="weui_panel_hd">{{title}}</div>
    <div class="weui_panel_bd">
      <div v-for="product in products" class="product_box weui_media_box weui_media_appmsg">
        <div class="weui_media_hd">
          <img class="weui_media_appmsg_thumb" v-bind:src="product.avatar" alt="">
        </div>
        <div class="weui_panel_bd">
          <div class="weui_media_box weui_media_text">
            <h4 class="weui_media_title">{{product.name}}</h4>
            <p class="weui_media_desc"> {{product.price}}</p>
            <div class="weui_media_info">
              <button class="button button-action button-circle button-tiny" @click="minus(product)">
                <i class="fa fa-minus"></i>
              </button>
              <button class="button button-action button-circle button-tiny add-product" @click="plus(product, $event)">
                <i class="fa fa-plus"></i>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props: ['title', 'products'],
    methods: {
      minus (product) {
        this.$dispatch('minus', product)
      },
      plus (product, event) {
        this.$dispatch('plus', product)
        console.log(event)
        var endTop = window.innerHeight - 30
        var endLeft = 20
        var left = event.x - event.offsetX
        var top = event.y + event.offsetY
        var outer = $('.point-pre').first().removeClass('point-pre').css({left: left + 'px', top: top + 'px'})
        var inner = outer.find('.point-inner')
        setTimeout(function () {
          outer[0].style.webkitTransform = 'translate3d(0,' + (endTop + top) + 'px,0)'
          inner[0].style.webkitTransform = 'translate3d(' + (endLeft - left) + 'px,0,0)'
          setTimeout(function () {
            outer.removeAttr('style').addClass('point-pre')
            inner.removeAttr('style')
          }, 800)
        }, 1)
      }
    }
  }
</script>
<style lang="scss">
.product_box {
  padding-top: 0px;
  padding-bottom: 0px;

  .weui_panel_bd {
    width: 100%;
    .weui_media_box {
      width: 100%;
      padding: 0px;
      .weui_media_title {
        margin: 0px;
        margin-top: 8px;
      }
      .weui_media_desc {
        font-size: 15px;
        color: red;
      }
      .weui_media_info {
        width: 100%;
        font-size: 13px;
        margin: 0px;
        .weui_media_info_meta_main {
          padding-top: 5px;
        }
        .weui_media_info_meta_extra {
          border-width: 0px;
          padding: 0px;
        }
      }
    }
  }

  .weui_media_hd {
    img {
      width: 80px;
      height: 80px;
    }
  }
}
</style>



