<template>
  <div class="weui_tab">
    <div class="weui_tab_bd"></div>
    <div class="weui_tabbar shopping-cart">
      <div class="weui_tabbar_item">
        <button class="button button-caution button-circle button-small" @click="showCart">
          <i class="fa fa-shopping-cart"></i>
        </button>
        <slot name="money"> 0</slot>
      </div>
      <div class="weui_tabbar_item check">
        <button @click="goCheck" class="button button-action"></button>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    methods: {
      showCart () {
        this.$dispatch('show-cart')
      },
      goCheck () {
        this.$dispatch('go-check')
      }
    }
  }
</script>
<style lang="scss">
  .shopping-cart { /* This will make your footer stay where it is */
    background: none repeat scroll 0 0 #FFF;
  	bottom: 0;
  	margin: 0;
  	padding: 0;
  	position: fixed;

    .cart {
      padding-left: 10px;
    }

    .check {
      text-align: right;
      padding-top: 0px;
    }
  }
</style>



